.testimonial-section {
  padding: 20px;
  max-width: 1000px;
  margin: auto;
  font-family: Arial, sans-serif;
}

.testimonial-header {
  font-size: 24px;
  color: #050a4e;
  font-weight: bold;
  margin-bottom: 20px;
  text-align: center;
}

.testimonial-grid {
  display: flex; /* Horizontal alignment */
  overflow-x: auto; /* Enable horizontal scrolling */
  gap: 20px; /* Add spacing between cards */
  padding: 10px 0; /* Padding for better spacing */
  scrollbar-width: thin; /* Firefox scrollbar */
  scrollbar-color: #ddd transparent; /* Firefox scrollbar colors */
}

.testimonial-grid::-webkit-scrollbar {
  height: 8px; /* Height of the scrollbar */
}

.testimonial-grid::-webkit-scrollbar-thumb {
  background: #bbb; /* Color of the scrollbar */
  border-radius: 4px; /* Rounded scrollbar edges */
}

.testimonial-grid::-webkit-scrollbar-track {
  background: transparent; /* Track color */
}

.testimonial-card {
  flex: 0 0 300px; /* Fixed width for horizontal scrolling */
  background-color: #fff;
  border: 1px solid #ddd;
  border-radius: 10px;
  padding: 15px;
  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.1);
  text-align: center;
}

.testimonial-message {
  font-size: 16px;
  font-style: italic;
  color: #333;
  margin-bottom: 15px;
}

.author-info {
  margin-top: 10px;
}

.author-name {
  font-size: 14px;
  font-weight: bold;
  color: #050a4e;
}

.author-location {
  font-size: 12px;
  color: #666;
}

.testimonial-date {
  font-size: 12px;
  color: #999;
  margin-top: 5px;
}

.loading,
.error {
  text-align: center;
  font-size: 18px;
  color: #f00;
  margin-top: 20px;
}
