/* Health Checkup Container */
/* General Container */
.health-checkup-container {
  padding: 20px;
  max-width: 1200px;
  margin: auto;
  font-family: Arial, sans-serif;
  background-color: #f4f8fc;
  border-radius: 12px;
}

/* Header Section */
.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
}

.header h2 {
  font-size: 24px;
  color: #0d1a57;
  font-weight: 700;
  margin: 0;
}

.view-all-button {
  color: #1778f2;
  font-weight: 600;
  background: none;
  border: none;
  font-size: 16px;
  cursor: pointer;
  padding: 5px 10px;
}

.view-all-button:hover {
  text-decoration: underline;
}

/* Categories Section */
.categories {
  margin-bottom: 30px; /* Space below categories */
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 10px;
  overflow-x: auto;
  overflow-y: hidden;
  padding: 10px;
}

.category-button {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 10px 15px;
  gap: 8px;

  background: #ffffff;
  border: 1px solid #f2f2f2;
  border-radius: 8px;
  cursor: pointer;

  font-family: "Lexend Deca", sans-serif;
  font-weight: 400;
  font-size: 12px;
  line-height: 15px;
  color: #000000;
}

.category-button.selected {
  border-color: #1778f2;
  background-color: #e7f3ff;
  color: #1778f2;
  font-weight: 600;
}

/* Scrollbar Styling for Categories */
.categories::-webkit-scrollbar {
  height: 6px;
}

.categories::-webkit-scrollbar-thumb {
  background-color: #1778f2;
  border-radius: 8px;
}

.categories::-webkit-scrollbar-track {
  background-color: #f4f8fc;
}

/* Health Checkup List Section */
.checkup-list {
  display: flex;
  overflow-x: auto;
  gap: 20px;
  padding-bottom: 10px;
}

/* Checkup Card */
.checkup-card {
  flex: 0 0 auto; /* Ensure cards maintain a consistent size */
  width: 300px;
  background-color: white;
  border-radius: 12px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
  padding: 20px;
  border-top: 5px solid #ff3b3b; /* Red bar for sponsored cards */
  position: relative;
}

/* Sponsored Tag */
.checkup-card .sponsored-tag {
  position: absolute;
  top: -10px;
  right: 10px;
  background-color: #ff3b3b;
  color: white;
  padding: 2px 8px;
  font-size: 12px;
  border-radius: 5px;
  font-weight: bold;
}

/* Card Header */
.card-header {
  margin-bottom: 12px;
}

.card-header h3 {
  font-size: 18px;
  color: #0d1a57;
  font-weight: 600;
}

.card-header .report-time {
  font-size: 14px;
  color: #1778f2;
  margin-top: 5px;
}

.card-header .report-time::before {
  content: "🕒";
  margin-right: 6px;
}

/* Test Count Section */
.test-count {
  font-size: 14px;
  color: #4f4f4f;
  margin: 10px 0;
}

/* Includes Section */
.includes-section {
  font-size: 14px;
  display: flex;
  justify-content: space-between;
  color: #0d1a57;
  background: #f9fbff;
  border-radius: 8px;
  padding: 10px;
  margin: 10px 0;
}

/* Price Section */
.price {
  font-size: 18px;
  color: #0d1a57;
  font-weight: bold;
}

.price .original-price {
  font-size: 14px;
  color: #999;
  text-decoration: line-through;
  margin-left: 8px;
}

/* Add to Cart Section */
.add-to-cart-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 5px 10px;
  border: 1px solid #e0e7ff;
  border-radius: 8px;
  margin-top: 10px;
}

.add-to-cart-container button {
  background: none;
  border: none;
  font-size: 16px;
  color: #1778f2;
  cursor: pointer;
}

.add-to-cart-container button:hover {
  color: #0d57b3;
}

/* Scrollbar Styling for Checkup List */
.checkup-list::-webkit-scrollbar {
  height: 8px;
}

.checkup-list::-webkit-scrollbar-thumb {
  background-color: #1778f2;
  border-radius: 10px;
}

.checkup-list::-webkit-scrollbar-track {
  background-color: #f4f8fc;
}
